window.hitokotoTimer = window.setInterval(showHitokoto,30000);

function showHitokoto(){
  $.getJSON('https://api.imjad.cn/hitokoto/?cat=&charset=utf-8&length=55&encode=json',function(result){
    showMessage(result.hitokoto, 5000);
  });
}

function showMessage(text, timeout, flag){
  if(flag || sessionStorage.getItem('waifu-text') === '' || sessionStorage.getItem('waifu-text') === null){
    if(Array.isArray(text)) text = text[Math.floor(Math.random() * text.length + 1)-1];
    //console.log(text);

    if(flag) sessionStorage.setItem('waifu-text', text);

    $('.waifu-tips').stop();
    $('.waifu-tips').html(text).fadeTo(200, 1);
    if (timeout === null) timeout = 5000;
    hideMessage(timeout);
  }
}
function hideMessage(timeout){
  $('.waifu-tips').stop().css('opacity',1);
  if (timeout === null) timeout = 5000;
  window.setTimeout(function() {sessionStorage.removeItem('waifu-text')}, timeout);
  $('.waifu-tips').delay(timeout).fadeTo(200, 0);
}
function formatSeconds(value) {
  var seconds = parseInt(value);// 秒
  var minutes = 0;
  var hours = 0;
  var days = 0;
  if(seconds > 60) {
    minutes = parseInt(seconds/60);
    seconds = parseInt(seconds%60);
    if(minutes > 60) {
      hours = parseInt(minutes/60);
      minutes = parseInt(minutes%60);
      if(hours > 24) {
        days = parseInt(hours/24);
        hours = parseInt(hours%24);
      }
    }
  }
  var result = "";
  if(minutes > 0)
    result = ""+parseInt(minutes)+"分";
  if(hours > 0 && hours <= 24)
    result = ""+parseInt(hours)+"小时"+result;
  if(days > 0)
    result = ""+parseInt(days)+"天"+result;
  return result;
}

function getChromeVersion(){
  let match = window.navigator.userAgent.match(/chrome\/(\d+)/i);
  if(match){
    return +match[1]
  }else{
    return null
  }
}
