import Vue from 'vue'
import Router from 'vue-router'
// 懒加载路由
const mainIndex = require('./views/mainIndex/Index.vue');
const themeIndex = require('./views/theme/themeIndex.vue');
const blogIndex = require('./views/blog/Index.vue');
Vue.use(Router)

export default new Router({
  // mode: 'history',
  // base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'main',
      component: (resolve: any) => require(['./views/main.vue'],resolve),
      children: [
        {
          path: '/mainIndex',
          name: 'mainIndex',
          component: (resolve: any) => require.ensure([],() => resolve(mainIndex))
        },
        {
          path: '/themeIndex',
          name: 'themeIndex',
          component: (resolve: any) => require.ensure([],() => resolve(themeIndex))
        },
        {
          path: '/blogIndex',
          name: 'blogIndex',
          component: (resolve: any) => require.ensure([],() => resolve(blogIndex))
        }
      ]
    }
  ]
})
